<template>
  <div class="login">
    <div style="width: 60%;margin-top: 20px;text-align: center;">
      <el-input v-model="username" placeholder="请输入用户名"></el-input>
      <el-input v-model="password" placeholder="请输入密码" show-password style="margin-top: 20px;"></el-input>
      <el-button type="primary" @click="onLogin" style="margin-top: 20px;">登 录</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      username: "ropon",
      password: "West.cn2020"
    };
  },
  methods: {
    onLogin() {
      let params = {
        username: this.username,
        password: this.password
      };
      this.$http
        .login(params)
        .then(res => {
          if (res.errcode === 0) {
            let otime = Math.round(new Date().getTime()/1000);
            let username = res.data.username;
            let token = res.data.token;
            localStorage.setItem("otime", otime);
            localStorage.setItem("username", username);
            localStorage.setItem("token", token);
            localStorage.setItem(
              "accesstoken",
              this.$md5(username + otime.toString() + token)
            );
            let user = {
              token: localStorage.getItem("token"),
              username: localStorage.getItem("username")
            };
            this.$store.dispatch("getUserInfo", user);
            this.$message({
              message: "登录成功",
              type: "success"
            });
            const redirect = this.$route.query.redirect;
            if (redirect) {
              //存在回跳地址就回跳
              this.$router.push(redirect);
            } else {
              //否则就跳到默认的首页
              this.$router.push({
                name: "Home"
              });
            }
          } else {
            this.$message.error(res.errmsg);
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  components: {}
};
</script>

<style>
.login {
  /* text-align: center; */
  padding: 30px;
  margin-bottom: 30px;
}
</style>



