<template>
  <div class="site">
    <el-table :data="sitelistData" style="width: 90%;margin-top: 20px;" v-show="is_sitelist">
      <el-table-column label="站点名" prop="site_name"></el-table-column>
      <el-table-column label="网站根目录" prop="root_path"></el-table-column>
      <el-table-column label="绑定域名" prop="bind_domain"></el-table-column>
      <el-table-column align="right">
        <template slot="header" slot-scope="">
          <el-input v-model="search" size="mini" placeholder="输入关键字搜索" />
        </template>
        <template slot-scope="scope">
          <el-button size="mini" icon="el-icon-edit" @click="handleEdit(scope.row.nid)"></el-button>
          <el-button
            type="danger"
            size="mini"
            icon="el-icon-delete"
            @click="handleDelete(scope.row.nid)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin-top: 20px;" v-show="is_sitelist">
      <el-button type="primary" icon="el-icon-arrow-left">上一页</el-button>
      <el-button type="primary">
        下一页
        <i class="el-icon-arrow-right el-icon--right"></i>
      </el-button>
    </div>
    <el-form
      ref="form"
      :model="form"
      label-width="100px"
      v-show="is_create"
      style="width: 90%;margin-top: 20px;"
    >
      <el-form-item label="站点名">
        <el-input v-model="form.site_name" placeholder="3-10个字母组成" @keyup.native="onGetinfo"></el-input>
      </el-form-item>
      <el-form-item label="绑定域名">
        <el-input v-model="form.bind_domain" placeholder="多个域名以英文逗号,分隔"></el-input>
      </el-form-item>
      <el-form-item label="网站根目录">
        <el-input v-model="form.root_path" placeholder="默认/home/wwwroot/站点名">
          <template slot="prepend">/home/wwwroot/</template>
        </el-input>
      </el-form-item>
      <el-form-item label="PHP版本">
        <el-select v-model="form.php_ver" placeholder="请选择PHP版本">
          <el-option label="php5.6" value="php5.6"></el-option>
          <el-option label="php7.0" value="php7.0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="开启SSL">
        <el-switch v-model="form.is_ssl"></el-switch>
      </el-form-item>
      <el-form-item label="开启日志">
        <el-switch v-model="form.log_path"></el-switch>
      </el-form-item>
      <el-form-item label="额外设置">
        屏蔽非常见UA
        <el-switch v-model="form.deny_ua"></el-switch>启用防盗链
        <el-switch v-model="form.deny_referer"></el-switch>安全加固
        <el-switch v-model="form.safe"></el-switch>开启缓存
        <el-switch v-model="form.cache"></el-switch>
      </el-form-item>
      <el-form-item label="网站备注">
        <el-input type="textarea" v-model="form.note"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onAddsite">创建站点</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>

    <el-form
      ref="form2"
      :model="form2"
      label-width="100px"
      v-show="is_update"
      style="width: 90%;margin-top: 20px;"
    >
      <el-form-item label="站点名">
        <el-input v-model="form2.site_name" placeholder="3-10个字母组成" disabled></el-input>
      </el-form-item>
      <el-form-item label="绑定域名">
        <el-input v-model="form2.bind_domain" placeholder="多个域名以英文逗号,分隔"></el-input>
      </el-form-item>
      <el-form-item label="网站根目录">
        <el-input v-model="form2.root_path" placeholder="必须是/home/wwwroot/xxxx" disabled></el-input>
      </el-form-item>
      <el-form-item label="PHP版本">
        <el-select v-model="form2.site_info.php_ver" placeholder="请选择PHP版本">
          <el-option label="php5.6" value="php5.6"></el-option>
          <el-option label="php7.0" value="php7.0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="开启SSL">
        <el-switch v-model="form2.site_info.is_ssl"></el-switch>
      </el-form-item>
      <el-form-item label="开启日志">
        <el-switch v-model="form2.site_info.log_path"></el-switch>
      </el-form-item>
      <el-form-item label="额外设置">
        屏蔽非常见UA
        <el-switch v-model="form2.site_info.extra_kwargs.deny_ua"></el-switch>启用防盗链
        <el-switch v-model="form2.site_info.extra_kwargs.deny_referer"></el-switch>安全加固
        <el-switch v-model="form2.site_info.extra_kwargs.safe"></el-switch>开启缓存
        <el-switch v-model="form2.site_info.extra_kwargs.cache"></el-switch>
      </el-form-item>
      <el-form-item label="网站备注">
        <el-input type="textarea" v-model="form2.note"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onUpsite">更新站点</el-button>
        <el-button @click="goback('update')">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "Site",
  data() {
    return {
      is_sitelist: true,
      sitelistData: [],
      search: "",
      nid: "",
      is_create: true,
      is_update: false,
      form: {
        site_name: "",
        bind_domain: "",
        root_path: "",
        is_ssl: false,
        log_path: false,
        deny_ua: true,
        deny_referer: true,
        safe: true,
        cache: true
      },
      form2: {
        site_info: {
          extra_kwargs: {}
        }
      }
    };
  },
  methods: {
    onGetsite() {
      this.$http
        .getsite(this.nid)
        .then(res => {
          this.errmsg = res.errmsg;
          if (res.errcode === 0) {
            this.sitelistData = res.data;
            if (this.nid != "") {
              this.form2 = res.data[0];
              this.is_create = false;
              this.is_update = true;
              this.is_sitelist = false;
            }
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    handleEdit(nid) {
      this.nid = nid;
      this.onGetsite();
    },
    handleDelete(nid) {
      let params = {
        nid: nid
      };
      this.$http
        .delsite(params)
        .then(res => {
          this.errmsg = res.errmsg;
          if (res.errcode === 0) {
            let data = res.data;
            console.log(data);
            this.onGetsite();
          }
        })
        .catch(error => {
          if (error.response) {
            this.errmsg = error.response.data.errmsg;
          } else {
            console.log(error.message);
          }
          console.log(error.config);
        });
    },
    onUpsite() {
      let params = {
        nid: this.nid,
        site_name: this.form2.site_name,
        root_path: this.form2.root_path,
        bind_domain: this.form2.bind_domain,
        site_info: {
          is_ssl: this.form2.site_info.is_ssl,
          php_ver: this.form2.site_info.php_ver,
          log_path: this.form2.site_info.log_path,
          extra_kwargs: {
            rewrite: "wordpress",
            deny_ua: this.form2.site_info.extra_kwargs.deny_ua,
            deny_referer: this.form2.site_info.extra_kwargs.deny_referer,
            safe: this.form2.site_info.extra_kwargs.safe,
            cache: this.form2.site_info.extra_kwargs.cache
          }
        }
      };
      this.$http
        .upsite(params)
        .then(res => {
          this.errmsg = res.errmsg;
          if (res.errcode === 0) {
            let data = res.data;
            this.$message({
              message: "更新站点成功",
              type: "success"
            });
            this.is_create = true;
            this.is_update = false;
            this.is_sitelist = true;
            this.nid = "";
            this.onGetsite();
          }
        })
        .catch(error => {
          if (error.response) {
            this.errmsg = error.response.data.errmsg;
            this.$message.error(this.errmsg);
          } else {
            console.log(error.message);
          }
          console.log(error.config);
        });
    },
    onAddsite() {
      if (this.form.log_path) {
        var log_path = "/home/weblogs/" + this.form.site_name;
      } else {
        var log_path = "";
      }
      let params = {
        site_name: this.form.site_name,
        root_path: "/home/wwwroot/" + this.form.root_path,
        bind_domain: this.form.bind_domain,
        site_info: {
          is_ssl: this.form.is_ssl,
          php_ver: this.form.php_ver,
          log_path: log_path,
          extra_kwargs: {
            rewrite: "",
            deny_ua: this.form.deny_ua,
            deny_referer: this.form.deny_referer,
            safe: this.form.safe,
            cache: this.form.cache
          }
        }
      };
      this.$http
        .addsite(params)
        .then(res => {
          this.errmsg = res.errmsg;
          if (res.errcode === 0) {
            let data = res.data;
            this.$message({
              message: "创建站点成功",
              type: "success"
            });
            this.onGetsite();
          }
        })
        .catch(error => {
          if (error.response) {
            this.errmsg = error.response.data.errmsg;
            console.log(123, this.errmsg);
            this.$message.error(this.errmsg);
          } else {
            console.log(error.message);
          }
          console.log(error.config);
        });
    },
    onGetinfo() {
      this.form.root_path = this.form.site_name;
      this.form.bind_domain = `${this.form.site_name}.top,www.${this.form.site_name}.top`;
    },
    goback(flag) {
      if (flag == "update") {
        this.nid = "";
      }
      this.is_update = false;
      this.is_create = true;
      this.is_sitelist = true;
      this.onGetsite(this.nid);
    }
  },
  created() {
    this.onGetsite();
  }
};
</script>

<style scoped>
.site {
  /* text-align: center; */
  padding: 30px;
  margin-bottom: 30px;
}
</style>


